<template>
  <!-- TIPO DE EQUIPOS -->
  <div
    class="pildora trans"
    :style="{ color: txtcolor, background: bgcolor }"
    v-if="tipo === 'tipo'"
  >
    <h1>{{ array.tipo }}</h1>
    <button>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-4 h-4"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
        @click="eliminarTipoEquipo(array)"
      >
        <circle cx="12" cy="12" r="10"></circle>
        <line x1="15" y1="9" x2="9" y2="15"></line>
        <line x1="9" y1="9" x2="15" y2="15"></line>
      </svg>
    </button>
  </div>

  <!-- EQUIPOS -->
  <div
    class="pildora trans"
    :style="{ color: txtcolor, background: bgcolor }"
    v-if="tipo === 'equipos'"
  >
    <h1>{{ array.equipo }}</h1>
    <button>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-4 h-4"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
        @click="eliminarEquipo(array)"
      >
        <circle cx="12" cy="12" r="10"></circle>
        <line x1="15" y1="9" x2="9" y2="15"></line>
        <line x1="9" y1="9" x2="15" y2="15"></line>
      </svg>
    </button>
  </div>

  <!-- DESTINOS -->
  <div
    class="pildora trans"
    :style="{ color: txtcolor, background: bgcolor }"
    v-if="tipo === 'destinos'"
  >
    <h1>{{ array.destino }}</h1>
    <button>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-4 h-4"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
        @click="eliminarDestinos(array)"
      >
        <circle cx="12" cy="12" r="10"></circle>
        <line x1="15" y1="9" x2="9" y2="15"></line>
        <line x1="9" y1="9" x2="15" y2="15"></line>
      </svg>
    </button>
  </div>

  <!-- USUARIOS -->
  <div
    class="pildora trans"
    :style="{ color: txtcolor, background: bgcolor }"
    v-if="tipo === 'usuariosTelegram'"
  >
    <h1>{{ array.destino + " ➤ " + array.usuario }}</h1>
    <button>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-4 h-4"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
        @click="eliminarUsuarioTelegram(array)"
      >
        <circle cx="12" cy="12" r="10"></circle>
        <line x1="15" y1="9" x2="9" y2="15"></line>
        <line x1="9" y1="9" x2="15" y2="15"></line>
      </svg>
    </button>
  </div>
</template>

<script>
import { useStore } from "vuex";
export default {
  props: {
    array: {
      type: Object,
      required: true,
    },
    tipo: {
      type: String,
      required: true,
    },
    bgcolor: {
      type: String,
      required: true,
    },
    txtcolor: {
      type: String,
      required: true,
    },
  },

  setup(props) {
    const store = useStore();

    // ELIMINA TIPOS DE EQUIPOS EN LA BITACORA
    const eliminarTipoEquipo = async (array) => {
      if (store.state.arrayBitacoras[0].tipos) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          idTipoEquipo: array.idTipoEquipo,
          action: "eliminarTipoEquipo",
          idBitacora: store.state.arrayBitacoras[0].idBitacora,
        });

        if (response) {
          store.state.arrayBitacoras[0].tipos.forEach((x, index, object) => {
            if (x.idTipoEquipo === array.idTipoEquipo) {
              object.splice(index, 1);
            }
          });
        }
      }
    };

    // ELIMINA EQUIPOS PARA ASIGNAR EN LA BITACORA
    const eliminarEquipo = async (array) => {
      if (store.state.arrayBitacoras[0].equipos) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          idEquipo: array.idEquipo,
          action: "eliminarEquipo",
          idBitacora: store.state.arrayBitacoras[0].idBitacora,
        });

        if (response) {
          store.state.arrayBitacoras[0].equipos.forEach((x, index, object) => {
            if (x.idEquipo === array.idEquipo) {
              object.splice(index, 1);
            }
          });
        }
      }
    };

    // ELIMINA DESTINOS ASIGNADOS EN LA BITACORA
    const eliminarDestinos = async (array) => {
      if (store.state.arrayBitacoras[0].destinos) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          idDestino: array.idDestino,
          action: "eliminarDestino",
          idBitacora: store.state.arrayBitacoras[0].idBitacora,
        });

        if (response) {
          store.state.arrayBitacoras[0].destinos.forEach((x, index, object) => {
            if (x.idDestino === array.idDestino) {
              object.splice(index, 1);
            }
          });
        }
      }
    };

    // ELIMINA USUARIOS TELEGRAM ASIGNADOS EN LA BITACORA
    const eliminarUsuarioTelegram = async (array) => {
      if (store.state.arrayBitacoras[0].parametros) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          idUsuario: array.idUsuario,
          action: "eliminarUsuarioTelegram",
          idParametro: array.idParametro,
        });

        if (response) {
          store.state.arrayBitacoras[0].parametros.forEach((item) => {
            if (item.idParametro === array.idParametro) {
              item.usuariosTelegram.forEach((item, index, object) => {
                if (item.idUsuario === array.idUsuario) {
                  object.splice(index, 1);
                }
              });
            }
          });
        }
      }
    };

    return {
      eliminarTipoEquipo,
      eliminarEquipo,
      eliminarDestinos,
      eliminarUsuarioTelegram,
    };
  },
};
</script>

<style scoped>
.pildora {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 2px;
  padding-bottom: 2px;
  border-radius: 9999px;
  font-weight: 500;
  font-size: 0.8rem;
  text-transform: uppercase;
  cursor: pointer;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.pildora button {
  margin-left: 10px;
  display: none;
}

.pildora:hover button {
  display: block;
}

.pildora:hover {
  --tw-shadow: 0 6px 15px 0 rgba(0, 0, 0, 0.144),
    0 1px 2px 0 rgba(0, 0, 0, 0.06);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
button:hover {
  color: #ff6868;
}
button:focus {
  outline: none;
  color: #ffacac;
}
</style>
