<template>
  <div class="relative">
    <div
      @click="lista = !lista"
      class="
        relative
        cursor-pointer
        bg-gray-100
        text-gray-500
        font-semibold
        uppercase
        px-2
        py-1
        text-xs
        mr-2
        mb-2
        w-8
        h-8
        rounded-full
        flex
        items-center
        justify-center
      "
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-6 h-6 flex-none"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        <circle cx="12" cy="12" r="10"></circle>
        <line x1="12" y1="8" x2="12" y2="16"></line>
        <line x1="8" y1="12" x2="16" y2="12"></line>
      </svg>
    </div>

    <div
      v-if="lista"
      class="absolute w-64 bg-white shadow rounded-lg p-2 z-50 text-sm"
    >
      <input
        type="search"
        v-model="palabra"
        @keyup="filtrarArray"
        class="
          w-full
          p-2
          bg-gris-30
          rounded-lg
          mb-2
          focus:outline-none
          ring-yellow-100
          focus:ring
        "
        placeholder="Buscar"
      />

      <!-- LISTA DE USUARIOS -->
      <div class="max-h-96" v-if="tipo === 'usuarios'">
        <button
          v-for="usuario in array"
          :key="usuario"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-b border-gris-30
          "
        >
          <div
            class="w-8 h-8 mr-2 rounded-full bg-cover bg-center"
            :style="{ backgroundImage: 'url(' + usuario.foto + ')' }"
          ></div>
          <div>
            <h1 class="truncate leading-none text-left">{{ usuario.texto }}</h1>
            <h1
              class="truncate text-xxs leading-3 text-left uppercase font-bold"
            >
              {{ usuario.puesto }}
            </h1>
          </div>
        </button>
      </div>

      <!-- LISTA DE TIPOS DE EQUIPOS -->
      <div class="max-h-96 scrolly" v-if="tipo === 'tiposEquipos'">
        <button
          v-for="(x, y) in arrayFiltrado"
          :key="y"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-b border-gris-30
          "
          @click="agregarTipoEquipo(x)"
        >
          <h1 class="truncate leading-none text-left">{{ x.tipo }}</h1>
        </button>
      </div>

      <!-- LISTA DE DESTINOS -->
      <div class="max-h-48 scrolly" v-if="tipo === 'destinos'">
        <button
          v-for="(x, y) in arrayFiltrado"
          :key="y"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-b border-gris-30
          "
          @click="agregarDestino(x)"
        >
          <h1 class="truncate leading-none text-left">{{ x.destino }}</h1>
        </button>
      </div>

      <!-- LISTA DE USUARIOS TELEGRAM -->
      <div class="max-h-52 scrolly" v-if="tipo === 'usuariosTelegram'">
        <button
          v-for="(x, y) in arrayFiltrado"
          :key="y"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-b border-gris-30
          "
          @click="agregarUsuarioTelegram(x)"
        >
          <h1 class="truncate leading-none text-left">
            {{ x.destino + " ➤ " + x.usuario }}
          </h1>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";

export default {
  props: {
    tipo: {
      type: String,
      required: true,
    },
    array: {
      type: Array,
      required: false,
    },
  },
  setup(props) {
    const store = useStore();

    const lista = ref(false);
    const palabra = ref("");
    const arrayFiltrado = ref(props.array);

    // EXCEPCIÓN PARA USUARIOS TELEGRAM
    if (props.tipo == "usuariosTelegram") {
      arrayFiltrado.value = props.array[1];
    }

    const filtrarArray = () => {
      // FILTRA TIPOS DE EQUIPOS
      if (props.tipo === "tiposEquipos") {
        arrayFiltrado.value = props.array.filter((x) => {
          return (
            x.tipo.toLowerCase().indexOf(palabra.value.toLowerCase()) !== -1
          );
        });
      }

      // FILTRA DESTINOS
      if (props.tipo === "destinos") {
        arrayFiltrado.value = props.array.filter((x) => {
          return (
            x.destino.toLowerCase().indexOf(palabra.value.toLowerCase()) !== -1
          );
        });
      }

      // FILTRA USUARIOS TELEGRAM
      if (props.tipo === "usuariosTelegram") {
        arrayFiltrado.value = props.array[1].filter((x) => {
          return (
            x.usuario.toLowerCase().indexOf(palabra.value.toLowerCase()) !== -1
          );
        });
      }
    };

    const agregarTipoEquipo = async (x) => {
      if (store.state.arrayBitacoras[0].tipos) {
        const existe = store.state.arrayBitacoras[0].tipos.find(
          (y) => y.idTipoEquipo == x.idTipoEquipo
        );

        if (!existe) {
          const response = await store.dispatch("accionesUsuario", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            idTipoEquipo: x.idTipoEquipo,
            action: "agregarTipoEquipo",
            idBitacora: store.state.arrayBitacoras[0].idBitacora,
          });

          if (response) {
            store.state.arrayBitacoras[0].tipos.push(x);
          }
        }
      }
    };

    // AGREGA DESTINO EN LA BITACORA
    const agregarDestino = async (array) => {
      if (store.state.arrayBitacoras[0].destinos) {
        const existe = store.state.arrayBitacoras[0].destinos.find(
          (y) => y.idDestino == array.idDestino
        );

        if (!existe) {
          const response = await store.dispatch("accionesUsuario", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            idDestino: array.idDestino,
            action: "agregarDestino",
            idBitacora: store.state.arrayBitacoras[0].idBitacora,
          });

          if (response) {
            store.state.arrayBitacoras[0].destinos.push(array);
          }
        }
      }
    };

    // AGREGA DESTINO EN LA BITACORA
    const agregarUsuarioTelegram = async (array) => {
      if (props.array[0].usuariosTelegram) {
        const existe = props.array[0].usuariosTelegram.find(
          (y) => y.idUsuario == array.idUsuario
        );

        if (!existe) {
          const response = await store.dispatch("accionesUsuario", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            action: "agregarUsuarioTelegram",
            idParametro: props.array[0].idParametro,
            idUsuario: array.idUsuario,
          });

          if (response) {
            if (store.state.arrayBitacoras[0].parametros) {
              store.state.arrayBitacoras[0].parametros.forEach((x) => {
                if (x.idParametro === props.array[0].idParametro) {
                  x.usuariosTelegram.push(array);
                }
              });
            }
          }
        }
      }
    };

    return {
      lista,
      filtrarArray,
      arrayFiltrado,
      palabra,
      agregarTipoEquipo,
      agregarDestino,
      agregarUsuarioTelegram,
    };
  },
};
</script>

<style scoped>
button:focus {
  background: #5d70fd;
  color: white;
  outline: none;
  border-radius: 0.6rem;
}

.scrolly {
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scroll-margin-left: 300px;
}

.scrolly::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}
</style>

