<template>
  <div class="flex items-center justify-center mr-2 text-xxs">
    <label class="switch" :class="{ activo: status }">
      <!-- FALSE -->
      <input type="checkbox" @click="$emit('eventChild')" v-if="!status" />

      <!-- TRUE -->
      <input
        type="checkbox"
        checked
        @click="$emit('eventChild')"
        v-if="status"
      />
      <div></div>
    </label>
    <h1 class="ml-1 font-semibold">{{ texto }}</h1>
  </div>
</template>

<script>
export default {
  name: "Toggle",
  emits: ["eventChild"],
  props: {
    texto: {
      type: String,
    },

    status: {
      type: Boolean,
    },
  },
};
</script>

<style scoped>
.switch input {
  position: absolute;
  opacity: 0;
}

.switch {
  display: inline-block;
  font-size: 20px; /* 1 */
  height: 1em;
  width: 2em;
  background: #bdb9a6;
  border-radius: 1em;
  cursor: pointer;
}
.activo {
  background: #72f65b;
}

.switch div {
  height: 1em;
  width: 1em;
  border-radius: 1em;
  background: #fff;
  box-shadow: 0 0.1em 0.3em rgba(0, 0, 0, 0.3);
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  transition: all 300ms;
}

.switch input:checked + div {
  -webkit-transform: translate3d(100%, 0, 0);
  -moz-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}
</style>
