<template>
  <div class="relative md:w-auto w-full">
    <div
      @click="lista = !lista"
      class="
        relative
        cursor-pointer
        bg-gray-100
        text-gray-500
        font-semibold
        uppercase
        px-2
        py-1
        text-xs
        mr-2
        mb-2
        w-8
        h-8
        rounded-full
        flex
        items-center
        justify-center
      "
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-6 h-6 flex-none"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        <circle cx="12" cy="12" r="10"></circle>
        <line x1="12" y1="8" x2="12" y2="16"></line>
        <line x1="8" y1="12" x2="16" y2="12"></line>
      </svg>
    </div>

    <div
      v-if="lista"
      class="
        absolute
        w-full
        md:w-auto
        bg-white
        shadow
        rounded-lg
        p-2
        z-50
        text-sm
        flex flex-row
        items-start
        justify-center
      "
    >
      <div class="w-1/3 md:w-32 flex-none max-h-96 scrolly">
        <button
          v-for="(x, y) in arraySecciones"
          :key="y"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-r border-gris-30
          "
          @click="
            arraySubsecciones = x.subsecciones;
            arrayEquipos = [];
          "
        >
          <h1 class="truncate leading-none text-left">{{ x.seccion }}</h1>
        </button>
      </div>

      <div
        class="w-1/3 md:w-40 flex-none max-h-96 scrolly"
        v-if="arraySubsecciones.length"
      >
        <button
          v-for="(x, y) in arraySubsecciones"
          :key="y"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-r border-gris-30
          "
          @click="obtenerEquipos(x)"
        >
          <h1 class="truncate leading-none text-left">{{ x.subseccion }}</h1>
        </button>
      </div>

      <div
        class="w-1/3 md:w-52 flex-none max-h-96 scrolly"
        v-if="arraySubsecciones.length"
      >
        <input
          type="search"
          placeholder="Buscar Equipo"
          class="
            w-auto
            p-2
            m-1
            bg-gris-30
            rounded-lg
            mb-2
            focus:outline-none
            ring-yellow-100
            focus:ring
          "
          v-model="equipo"
          @keyup="buscarEquipo"
        />
        <button
          v-for="(x, y) in arrayEquipos"
          :key="y"
          class="
            w-full
            px-2
            pt-3
            pb-3
            hover:bg-gris-30
            flex flex-row
            items-center
            justify-start
            border-gris-30
          "
          @click="agregarEquipo(x)"
        >
          <h1 class="truncate leading-none text-left">
            {{ x.destino + " ➤ " + x.equipo }}
          </h1>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
export default {
  props: {
    arreglo: {
      type: Array,
      value: [1, 2, 3, 4, 5],
      required: false,
    },
  },
  setup() {
    const store = useStore();

    const lista = ref(false);
    const vista = ref("secciones");
    const arraySubsecciones = ref([]);
    const arrayEquiposFull = ref([]);
    const arrayEquipos = ref([]);
    const equipo = ref("");

    const arraySecciones = computed(() => {
      return store.state.arraySecciones;
    });

    const obtenerEquipos = async (array) => {
      const response = await store.dispatch("getEquipos", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idSeccion: array.idSeccion,
        idSubseccion: array.idSubseccion,
        action: "equipos",
        idBitacora: store.state.arrayBitacoras[0].idBitacora,
      });

      if (response.length) {
        arrayEquipos.value = response;
        arrayEquiposFull.value = response;
      } else {
        arrayEquipos.value = [
          { idEquipo: 0, equipo: "SIN EQUIPOS", destino: "AME" },
        ];
      }
    };

    // BUSCA EQUIPO EN LA LISTA OBTENIDA SEGÚN SECCIÓN Y SUBSECCIÓN
    const buscarEquipo = () => {
      arrayEquipos.value = arrayEquiposFull.value.filter((x) => {
        return (
          x.equipo.toLowerCase().indexOf(equipo.value.toLowerCase()) !== -1
        );
      });
    };

    // AGREGA EQUIPO A LA BITACORA
    const agregarEquipo = async (array) => {
      if (store.state.arrayBitacoras[0].equipos) {
        const existe = store.state.arrayBitacoras[0].equipos.find(
          (y) => y.idEquipo == array.idEquipo
        );

        if (!existe) {
          const response = await store.dispatch("accionesUsuario", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            idEquipo: array.idEquipo,
            action: "agregarEquipo",
            idBitacora: store.state.arrayBitacoras[0].idBitacora,
          });

          if (response) {
            store.state.arrayBitacoras[0].equipos.push(array);
          }
        }
      }
    };

    return {
      lista,
      vista,
      arraySecciones,
      arraySubsecciones,
      arrayEquipos,
      obtenerEquipos,
      buscarEquipo,
      equipo,
      agregarEquipo,
    };
  },
};
</script>

<style scoped>
button:focus {
  background: #5d70fd;
  color: white;
  outline: none;
  border-radius: 0.6rem;
}

.scrolly {
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scroll-margin-left: 300px;
}

.scrolly::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}
</style>
