import { createStore } from 'vuex'

export default createStore({
  state: {

    arrayBitacoras: [],
    arrayTiposEquipos: [],
    arraySecciones: [],
    arrayEquipos: [],
    arrayDestinos: [],
    arrayUnidades: [],
    arrayUsuariosTelegram: [],

  },

  mutations: {
    setBitacoras(state, payload) {
      state.arrayBitacoras = payload;
    },

    setTiposEquipos(state, payload) {
      state.arrayTiposEquipos = payload;
    },

    setSecciones(state, payload) {
      state.arraySecciones = payload;
    },

    setEquipos(state, payload) {
      state.arrayEquipos = payload;
    },

    setDestinos(state, payload) {
      state.arrayDestinos = payload;
    },

    setUnidades(state, payload) {
      state.arrayUnidades = payload;
    },

    setUsuariosTelegram(state, payload) {
      state.arrayUsuariosTelegram = payload;
    }

  },

  actions: {

    // OBTIENE LAS BITACORAS
    async getBitacoras({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();

        if (data.bitacoras) {
          commit("setBitacoras", data.bitacoras);
        }

        if (data.tiposEquipos) {
          commit("setTiposEquipos", data.tiposEquipos);
        }

        if (data.seccionesSubsecciones) {
          commit("setSecciones", data.seccionesSubsecciones);
        }

        if (data.destinos) {
          commit("setDestinos", data.destinos);
        }

        if (data.unidades) {
          commit("setUnidades", data.unidades);
        }

        if (data.usuariosTelegram) {
          commit("setUsuariosTelegram", data.usuariosTelegram);
        }

      } catch (error) {
        console.log(error);
      }
    },

    // ACCIONES DE USUARIO
    async accionesUsuario({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return true;
        }
        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },

    // ACCIONES DE USUARIO
    async getEquipos({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          commit("setEquipos", data.equipos);
          return await data.equipos;
        }
        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },





  },
  modules: {
  }
})
