<script>
import { onMounted, computed } from "@vue/runtime-core";
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { useRoute } from "vue-router";

import Parametro from "../components/Parametro.vue";
import Pill from "../components/Pill.vue";
import Toggle from "../components/Toggle.vue";
import Btnequipos from "../components/Btnequipos.vue";
import Botonadd from "../components/Botonadd.vue";

export default {
  components: { Toggle, Pill, Parametro, Btnequipos, Botonadd },

  setup() {
    const store = useStore();
    const route = useRoute();
    const uniqid = require("uniqid");

    const arrayParametro = ref([]);
    const nuevoParametro = ref("");

    onMounted(() => {
      store.dispatch("getBitacoras", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "bitacoras",
        idBitacora: route.params.idBitacora,
      });
    });

    const arrayBitacoras = computed(() => {
      return store.state.arrayBitacoras;
    });

    const arrayTiposEquipos = computed(() => {
      return store.state.arrayTiposEquipos;
    });

    const arrayDestinos = computed(() => {
      return store.state.arrayDestinos;
    });

    const arrayUnidades = computed(() => {
      return store.state.arrayUnidades;
    });

    const arrayUsuariosTelegram = computed(() => {
      return store.state.arrayUsuariosTelegram;
    });

    // ACTUALIZA BITACORA
    const actualizarBitacora = async (opcion, value) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "actualizarBitacora",
        opcion: opcion,
        value: value,
        idBitacora: store.state.arrayBitacoras[0].idBitacora,
      });

      if (response) {
        console.log("ok");
      } else {
        console.log("error");
      }
    };

    // AGREGA PARAMETROS A LA BITACORA
    const agregarParametro = async () => {
      if (
        store.state.arrayBitacoras[0].idBitacora &&
        nuevoParametro.value.length
      ) {
        const idParametro = uniqid(uniqid());

        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "agregarParametro",
          idParametro: idParametro,
          parametro: nuevoParametro.value,
          idBitacora: store.state.arrayBitacoras[0].idBitacora,
        });

        if (response) {
          if (store.state.arrayBitacoras[0].parametros) {
            await store.state.arrayBitacoras[0].parametros.push({
              idParametro: idParametro,
              parametro: nuevoParametro.value,
              configuracionGlobal: true,
              idUnidad: "0",
              fechaInicio: "",
              horaInicio: "",
              horas: "0",
              dias: "0",
              semanas: "0",
              meses: "0",
              parametroMinimo: "0",
              parametroMaximo: "0",
              generarIncidencia: false,
              tituloIncidencia: "",
              tipoIncidencia: "",
              activo: "1",
              change: 0,
            });
          }
          nuevoParametro.value = "";
        } else {
          console.log("error");
        }
      } else {
        console.log("Vacío");
      }
    };

    // ACTUALIZA PARAMETRO
    const actualizarParametro = async (array, posicion) => {
      if (array.change <= 0) {
        console.log("No Hay Cambios");
        return;
      }

      // VALIDACIÓN DE DATOS NIVEL 1
      if (
        !array.configuracionGlobal &&
        (array.fechaInicio.length || array.horaInicio.length) &&
        array.horas <= 0 &&
        array.dias <= 0 &&
        array.semanas <= 0 &&
        array.meses <= 0
      ) {
        console.log("Vacio 1");
        return;
      }

      // VALIDACIÓN DE DATOS NIVEL 2
      if (
        !array.parametro.length ||
        array.idUnidad <= 0 ||
        isNaN(array.parametroMinimo) ||
        isNaN(array.parametroMaximo)
      ) {
        console.log("Vacio 2");
        return;
      }

      // VALIDACIÓN DE DATOS NIVEL 3
      if (
        array.generarIncidencia &&
        (!array.tituloIncidencia.length || !array.tipoIncidencia.length)
      ) {
        console.log("Vacio 3");
        return;
      }

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "actualizarParametro",
        idParametro: array.idParametro,
        parametro: array.parametro,
        idUnidad: array.idUnidad,
        configuracionGlobal: array.configuracionGlobal,
        fechaInicio: array.fechaInicio,
        horaInicio: array.horaInicio,
        horas: array.horas,
        dias: array.dias,
        semanas: array.semanas,
        meses: array.meses,
        parametroMinimo: array.parametroMinimo,
        parametroMaximo: array.parametroMaximo,
        parametroMaximo: array.parametroMaximo,
        generarIncidencia: array.generarIncidencia,
        tituloIncidencia: array.tituloIncidencia,
        tipoIncidencia: array.tipoIncidencia,
        activo: array.activo,
      });

      if (response) {
        console.log("Actualizado");
        if (store.state.arrayBitacoras[0].parametros) {
          store.state.arrayBitacoras[0].parametros.forEach((x, y) => {
            if (x.idParametro === array.idParametro) {
              x.change = 0;
            }
          });
        }
      } else {
        console.log("error");
      }
    };

    return {
      arrayBitacoras,
      arrayParametro,
      arrayTiposEquipos,
      arrayDestinos,
      actualizarBitacora,
      agregarParametro,
      nuevoParametro,
      arrayUnidades,
      actualizarParametro,
      arrayUsuariosTelegram,
    };
  },
};
</script>

